* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html, body {
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
}

.container {
  margin: 0 auto;
  max-width: 1352px;
}
@media (max-width: 1352px) {
  .container {
    width: 100%;
    padding: 0 1rem;
  }
}

.header {
  height: 4.375rem;
  width: 100%;
  background: #fff;
  box-shadow: 0 3px 6px #00000029;
  position: fixed;
  top: 0;
  z-index: 10;
  opacity: 0.95;
}
.header_container {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_logo {
  color: #08A6E4;
  font-family: "Raleway", sans-serif;
  font-size: 2rem;
  font-weight: 300;
  line-height: 2rem;
}
.header nav {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header nav #menu {
  appearance: none;
}
.header nav #menu:checked ~ .menu .fas {
  display: none;
}
.header nav #menu:checked ~ .menu .fa-solid {
  display: block;
}
.header nav #menu:checked ~ ul {
  transform: translateX(0);
}
.header nav .menu {
  color: #08A6E4;
  cursor: pointer;
  font-size: 1.25rem;
  padding: 0.5rem 1rem;
  position: fixed;
  top: 1rem;
  right: 1rem;
}
.header nav .menu .fa-solid {
  display: none;
}
.header nav .nav_list {
  box-shadow: 0 3px 6px #00000029;
  position: fixed;
  top: 5rem;
  left: 0;
  background-color: #fff;
  width: 100%;
  height: 27%;
  transform: translateX(-100%);
  transition: 0.2s;
  padding-top: 1rem;
}
.header nav .nav_list a {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 0.7rem;
  letter-spacing: 0.2rem;
  line-height: 1rem;
  color: #A5A5A5;
  padding: 0.75rem 1rem;
}
.header nav .nav_list a:hover {
  color: #000;
}
@media (min-width: 790px) {
  .header nav .menu {
    display: none;
  }
  .header nav .nav_list {
    display: flex;
    background-color: transparent;
    height: auto;
    width: auto;
    transform: none;
    position: static;
    box-shadow: none;
    gap: 4rem;
    padding-top: 0;
  }
  .header nav .nav_list a {
    display: inline;
    padding: 1.5rem 1rem;
  }
  .header nav .nav_list a:hover {
    border-bottom: 4px solid #08A6E4;
  }
}